@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap");
@import url(./vendor/swiper-bundle.css);
:root {
  --color-black: #100d0d;
  --color-white: #fff;
  --color-orange: #f9af31;
  --color-orange-200: #ffe4b7;
  --color-orange-100: #fef7ea;
  --color-gray-800: #777777;
  --color-gray-500: #b2b2b2;
  --color-gray-200: #f7f7f7;
  --color-gray-100: #eaeaea;
  --color-red: #cf1515;
  --color-green: #269c47;
  --color-orange-linear: linear-gradient(278.62deg, #fce4bb 1.35%, #fef7ea 113.81%);
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

button {
  cursor: pointer;
  border: 1px solid transparent;
  background-color: transparent;
}

address {
  font-style: normal;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: 'Montserrat', sans-serif;
  background-color: var(--color-white);
  color: var(--color-black);
  line-height: 1.3;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
}

section {
  margin-bottom: 150px;
}

.container {
  margin: 0 auto;
  padding: 0 22px;
  max-width: 1284px;
  width: 100%;
}

.error {
  border: 2px #ff4019 solid;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: transparent;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.error {
  border: 2px #ff4019 solid;
}

.main {
  flex-grow: 1;
}

.container,
.wrapper {
  max-width: 1130px;
  width: 100%;
  padding: 0 15px;
  margin: 0 auto;
}

.wrapper {
  max-width: 1290px;
}

.title {
  font-size: 42px;
  margin-bottom: 15px;
  color: var(--color-black);
}

.title-second {
  font-size: 32px;
  margin-bottom: 15px;
}

.title-third {
  font-size: 18px;
  color: var(--color-black);
}

.slogan {
  font-size: 14px;
  color: var(--color-gray-800);
}

.text {
  font-weight: 400;
  font-size: 16px;
  color: var(--color-gray-800);
  letter-spacing: 0.03em;
  line-height: 1.7;
}

.sprite {
  width: 25px;
  height: 25px;
  fill: transparent;
  stroke: var(--color-black);
  stroke-width: 2px;
  transition: stroke 0.3s ease-in;
}

.btn {
  text-transform: uppercase;
  padding: 15px 30px;
  background-color: var(--color-black);
  color: var(--color-white);
  border-radius: 5px;
}

.background {
  position: relative;
  z-index: 0;
  overflow: hidden;
  border-radius: 5px;
}

.background:after {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  border-radius: 20px;
  border: 1px solid rgba(249, 175, 49, 0.3);
  left: 0;
  background: var(--color-orange-linear);
  z-index: -1;
}

.background:before {
  content: "";
  position: absolute;
  background-image: url("../img/petals.png");
  height: 370px;
  width: 460px;
  bottom: 0;
  left: 0;
  z-index: 0;
}

.header {
  border-bottom: 1px solid rgba(249, 175, 49, 0.3);
}

.navbar {
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-weight: 600;
}

.navbar__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar__item:not(:last-child) {
  margin-right: 50px;
}

.navbar__link {
  letter-spacing: 0.02em;
  text-transform: capitalize;
}

.story {
  height: 530px;
  overflow: hidden;
}

.story__list {
  display: block;
}

.story__item {
  background-color: var(--color-orange-100);
  border-radius: 0px 0px 20px 20px;
  position: relative;
  padding: 140px 0 50px 80px;
  height: 100% !important;
}

.story__item:before, .story__item:after {
  content: "";
  position: absolute;
  background-color: var(--color-orange);
  border-radius: 5px 5px 0px 0px;
  width: 2px;
  left: 80px;
  bottom: 0;
}

.story__item:before {
  height: 40px;
}

.story__item:after {
  height: 80px;
  background-color: var(--color-orange);
  opacity: 0.2;
}

.story .title {
  color: var(--color-orange);
}

.story .title__word {
  color: var(--color-black);
}

.story__inner {
  max-width: 445px;
}

.story__text {
  margin-right: 75px;
  line-height: 1.6;
  margin-bottom: 65px;
}

.story__more {
  margin-left: 15px;
}

.story__img {
  max-width: 790px;
  width: 100%;
  position: absolute;
  right: 0;
  top: 90px;
}

.story .swiper-pagination {
  height: 4px;
  background-color: transparent;
  left: 0;
  right: 0;
}

.story .swiper-pagination .swiper-pagination-progressbar-fill {
  background: linear-gradient(270deg, #F9AF31 0%, rgba(249, 175, 49, 0) 100%);
}

.services .slogan {
  margin-bottom: 35px;
}

.services__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.services__item {
  max-width: 350px;
  width: 100%;
  border: 1px solid var(--color-gray-100);
  border-radius: 20px;
  background-color: var(--color-gray-200);
  padding: 45px 35px 35px;
  overflow: hidden;
  position: relative;
  z-index: 0;
  cursor: default;
}

.services__item:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  background: var(--color-orange-linear);
  left: 0;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s linear;
}

.services__item:after {
  content: "";
  position: absolute;
  height: 45px;
  width: 45px;
  background-color: var(--color-black);
  background-image: url("../img/sprite.svg#build");
  background-size: 30px;
  background-position: center;
  background-repeat: no-repeat;
  top: 35px;
  border-radius: 5px;
  transition: background-color 0.3s linear;
}

.services__item--build:after {
  background-image: url("../img/sprite.svg#build");
}

.services__item--house:after {
  background-image: url("../img/sprite.svg#house");
}

.services__item--plug:after {
  background-image: url("../img/sprite.svg#plug");
}

.services__item:not(:last-child) {
  margin-right: 10px;
}

.services__item:hover:before {
  opacity: 1;
}

.services__item:hover:after {
  background-color: var(--color-orange);
}

.services__title {
  padding-left: 60px;
  margin-bottom: 35px;
}

.services__text {
  font-size: 14px;
  line-height: 1.7;
  margin-bottom: 25px;
  height: 80px;
}

.services__link {
  display: flex;
  align-items: center;
  font-weight: 600;
  width: fit-content;
  transition: transform 0.3s ease-in, color 0.3s ease-in;
}

.services__link:hover {
  transform: translate(10px);
  color: var(--color-orange);
}

.services__link:hover .sprite {
  stroke: currentColor;
}

.services__icon {
  margin-right: 10px;
}

.about {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.about__inner {
  margin-right: 50px;
}

.about__text {
  max-width: 510px;
  width: 100%;
  line-height: 1.7;
  margin-bottom: 60px;
}

.about__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-black);
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 55px;
}

.about__list span {
  color: var(--color-orange);
  display: block;
  font-size: 44px;
  margin-bottom: 20px;
}

.about__item {
  position: relative;
}

.about__item:not(:last-child) {
  margin-right: 90px;
}

.about__item:not(:last-child):after {
  content: "";
  position: absolute;
  height: 100%;
  right: -45px;
  top: 0;
  width: 1px;
  background-color: var(--color-gray-100);
}

.about__founder {
  display: flex;
  align-items: center;
}

.about__founder h3 {
  margin-bottom: 5px;
}

.about__founder-img {
  width: 60px;
  height: 60px;
  border-radius: 5px;
  display: inline-block;
  margin-right: 15px;
}

.about__image {
  border: 1px solid rgba(249, 175, 49, 0.3);
  border-radius: 20px;
  background: var(--color-orange-linear);
  max-width: 460px;
  width: 100%;
  overflow: hidden;
  padding-top: 35px;
}

.about__image img {
  max-width: 530px;
}

.clients__list {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 60px;
}

.clients__item {
  display: flex;
  position: relative;
  z-index: 1;
  flex-direction: column;
  padding: 20px 25px 25px 20px;
  max-width: 315px;
  width: 100%;
  background-color: var(--color-white);
  box-shadow: 0px 16px 24px rgba(249, 175, 49, 0.06), 0px 2px 6px rgba(249, 175, 49, 0.04), 0px 0px 1px rgba(249, 175, 49, 0.04);
  border-radius: 20px;
}

.clients__item:not(:last-child) {
  margin-right: 20px;
}

.clients .text {
  margin-bottom: 25px;
  font-size: 14px;
}

.clients .title-third {
  margin-bottom: 10px;
}

.contact {
  width: 100%;
  padding: 60px;
}

.contact.background:after {
  transform: matrix(-1, 0, 0, 1, 0, 0);
}

.contact.background:before {
  top: 0;
  right: 0;
  left: auto;
  transform: rotate(180deg) scale(1.1);
}

.contact__inner {
  max-width: 460px;
  width: 100%;
  margin-left: auto;
  position: relative;
}

.contact__inner .text-center {
  margin-bottom: 40px;
}

.contact__img {
  position: absolute;
  width: auto;
  height: auto;
  left: 0;
}

.contact__img--key {
  top: 50px;
}

.contact__img--free {
  bottom: 0;
}

.contact .slogan {
  margin-bottom: 40px;
}

.form {
  width: 100%;
}

.form__area {
  display: block;
  width: 100%;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 1.7;
  border: 1px solid transparent;
}

.form__area--text {
  min-height: 150px;
  resize: none;
}

.form__btn {
  width: 100%;
}

.footer__nav {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  padding: 40px 0;
}

.footer__navbar {
  padding: 0;
}

.footer__navbar .navbar__list {
  display: block;
  column-count: 2;
}

.footer__navbar .navbar__item {
  margin-bottom: 24px;
  margin-right: 90px;
}

.footer__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__list {
  display: flex;
  align-items: center;
}

.footer__list.messanger {
  color: var(--color-black);
}

.footer__list.messanger .footer__item:not(:last-child) {
  margin-right: 20px;
}

.footer__list.messanger .footer__item:not(:last-child):before {
  content: "";
  position: absolute;
  height: 6px;
  width: 6px;
  background-color: var(--color-black);
  right: -12px;
  border-radius: 50%;
  top: 50%;
  transform: translateY(-50%);
}

.footer__item {
  position: relative;
}

.footer__item:not(:last-child) {
  margin-right: 20px;
}

.footer__item:not(:last-child):before {
  content: "";
  position: absolute;
  height: 100%;
  width: 1px;
  background-color: var(--color-gray-500);
  right: -10px;
}

.footer__bottom {
  padding: 40px 0;
  border-top: 1px solid var(--color-gray-100);
}

.footer .address {
  display: flex;
}

.footer .address__inner {
  max-width: 200px;
  width: 100%;
  position: relative;
}

.footer .address__inner:before {
  content: "";
  position: absolute;
  height: 50px;
  width: 50px;
  border: 1px solid var(--color-orange-200);
  border-radius: 5px;
  left: -65px;
  background-position: center;
  background-size: 20px;
  background-repeat: no-repeat;
}

.footer .address__inner--tel:before {
  background-image: url("../img/sprite.svg#phone");
}

.footer .address__inner--pin:before {
  background-image: url("../img/sprite.svg#map-pin");
}

.footer .address__inner:not(:last-child) {
  margin-right: 150px;
}

.footer .address .slogan {
  display: block;
  line-height: 2;
}

.footer .address__link {
  font-weight: 600;
}
